</template>
<script>
import api from '../../api'
export default {
  data () {
    return {
      user: {},
      shop: {},
      logId: ''
    }
  },
  created () {
    this.get()
  },
  methods: {
    get () {
      this.$http.get(api.getUser()).then((res) => {
        this.user = res.data.attributes.data
      })
      this.$http.get(api.getOrderInfo()).then((res) => {
        this.shop = res.data.attributes.data
      })
    }
  }
}
</script>
